import {NgModule} from "@angular/core";
import {RouterModule} from "@angular/router";
import {CommonModule} from "@angular/common";
import {FormsModule} from "@angular/forms";
import {HttpModule} from "@angular/http"
import {SiteListComponent} from "./site-list/site-list.component";
import { SiteEditComponent } from './site-edit/site-edit.component';
import { SiteAddComponent } from './site-add/site-add.component';
import { SiteUIAddComponent } from './site-ui-add/site-ui-add.component';
import { SiteUIListComponent } from './site-ui-list/site-ui-list.component';

const SYSTEM_SITES_ROUTES = [
    {path: '', component: SiteListComponent, pathMatch: 'full'},
    {path: 'add', component: SiteAddComponent, pathMatch: 'full'},
    {path: ':id/edit', component: SiteEditComponent, pathMatch: 'full'},
    {path: ':id/ui', component: SiteUIListComponent, pathMatch: 'full'},
    {path: ':id/ui/add', component: SiteUIAddComponent, pathMatch: 'full'},
];

@NgModule({
    declarations: [
        SiteListComponent,
        SiteEditComponent,
        SiteAddComponent,
        SiteUIAddComponent,
        SiteUIListComponent
    ],
    imports: [
        CommonModule,
        FormsModule,
        HttpModule,
        RouterModule.forChild(SYSTEM_SITES_ROUTES)
    ]
})
export class SystemSitesModule {

}



